import React from 'react';

import './MovieListTitle.scss';

import { getSearchQuery } from '~/utils/getSearchQuery';

const MovieListTitle = ({ page, listType }) => {
  let titleContent = '  ';

  switch (listType) {
    case 'popular':
      titleContent = '  ';
      break;

    case 'best':
      titleContent = '  ';
      break;

    case 'newest':
      titleContent = '  ';
      break;

    default:
      titleContent = `  : ${getSearchQuery(listType)}`;
      break;
  }

  return (
    <>
      <h1 className="movie-list__subtitle title">{titleContent}</h1>
      <h3 className="movie-list__subtitle title">{` : ${page}`}</h3>
    </>
  );
};

export default MovieListTitle;
